import { Component, OnInit } from '@angular/core';
import * as html2canvas from 'html2canvas';
@Component({
  selector: 'app-html2canvas',
  templateUrl: './html2canvas.component.html',
  styleUrls: ['./html2canvas.component.less']
})
export class Html2canvasComponent implements OnInit {
  style = 'wrap-1';
  styles = [
    { value: 'wrap-1', label: '1' },
    { value: 'wrap-2', label: '2' },
  ];
  data = [
    {
      rank_change: 0,
      writer_name: '',
      opus: '',
      rank: 9.8
    },
    {
      rank_change: 1,
      writer_name: '',
      opus: '',
      rank: 8.8
    },
    {
      rank_change: null,
      writer_name: '',
      opus: '',
      rank: 9
    },
    {
      rank_change: 3,
      writer_name: '',
      opus: '',
      rank: 8.7
    },
    {
      rank_change: 0,
      writer_name: '',
      opus: '',
      rank: 8.6
    },
    {
      rank_change: 0,
      writer_name: '',
      opus: '',
      rank: 8.5
    },
    {
      rank_change: null,
      writer_name: '',
      opus: '',
      rank: 8
    },
    {
      rank_change: 5,
      writer_name: '',
      opus: '',
      rank: 6.5
    },
    {
      rank_change: 0,
      writer_name: '',
      opus: '',
      rank: 6.3
    },
    {
      rank_change: 0,
      writer_name: '',
      opus: '',
      rank: 6
    }
  ];
  canvasImg: '';
  constructor() { }

  ngOnInit() {
  }
  saveImgLocal() {
    const shareContent = document.getElementById('wrap'); // DOM 
    const width = shareContent.offsetWidth; // dom 
    const height = shareContent.offsetHeight; // dom 
    const canvas = document.createElement('canvas'); // canvas
    const scale = 2; //  
    canvas.width = width * scale; // canvas  * 
    canvas.height = height * scale; // canvas *
    canvas.getContext('2d').scale(scale, scale); // context,scale
    const opts = {
      scale: scale, // scale 
      canvas: canvas, //  canvas
      logging: true, // 
      width: width, // dom 
      height: height, // dom 
      useCORS: true // 
    };
    // tslint:disable-next-line:no-shadowed-variable
    html2canvas(shareContent, opts).then((canvas: any) => {
      this.canvasImg = canvas.toDataURL('image/png');
      this.downloadFile('', this.canvasImg);
    });

  }
  // filename:  content: canvas
  downloadFile(filename, content) {
    const base64Img = content;
    const oA = document.createElement('a');
    oA.href = base64Img;
    oA.download = filename;
    const event = document.createEvent('MouseEvents');
    event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
    oA.dispatchEvent(event);
  }
  download() {
    this.downloadFile('', this.canvasImg);
  }


}
